export default {
	props: {
		item: {
			type: Object
		},
		index: {
			type: Number
		}
	},	
	data() {
		return {
			isGuangzhu: this.item.isGuangzhu,
			infonum: this.item.infonum
		}
	},
	methods: {
		// 关注
		guanzhu() {
			this.isGuangzhu = true
			uni.showToast({
				title: '关注成功！'
			});
		},
		// 哭脸笑脸操作
		operator(type) {
			switch (type) {
				case 1:
					// 如果已经顶过了
					if (this.infonum.index === 1) return
					// 如果是没顶过的		
					this.infonum.dingnum++						
					// 如果踩过 就需要减1
					if (this.infonum.index === 2) {
						this.infonum.cainum--
					}
					this.infonum.index = 1						
					break;
				case 2:
					// 如果已经踩过了
					if (this.infonum.index === 2) return
					// 如果是没顶过的						
					this.infonum.cainum++
					// 如果踩过 就需要减1
					if (this.infonum.index === 1) {
						this.infonum.dingnum--
					}
					this.infonum.index = 2						
					break;
			}
		},
		// 跳转到详情页
		openDetail() {
			uni.navigateTo({
				url: `../../pages/detail/detail?detailData=${JSON.stringify(this.item)}`,
			});
		}
	}
}
